@import '_styles/variables';
@import '_styles/mixins';

.HeroArticle {
  padding: calc(12px + 2vw) 0;
  background: transparent url('../_images/misc/bannerPattern.png') repeat;

  &__grid {
    grid-auto-flow: row;

    @include respondAbove (m) {
      grid-auto-flow: column;
    }
  }

  &__image {

    &:hover {
      opacity: 0.8;
    }
  }

  &__text {
    align-self: center;
  }

  &__title {
    margin: 0 0 1.25rem;
    line-height: 1.25em;
  }

  &__description {
    @include fontSubtitle;
    margin: 0 0 1.25rem;
    opacity: 0.6;
  }

  &__timeAgo {
    @include fontCaps;
    opacity: 0.2;
    margin: 0 0 2vw;
    font-size: 0.625em;
  }
}
