//
//
// Anchors
//
//

.anchor {
  color: $color__default;
  transition: opacity 0.2s $romiem-easingCurve;
  text-decoration: none;
  border-bottom: 1px solid currentColor;

  &:hover {
    opacity: 0.5;
  }

  &--noUnderline {
    border-bottom: 0;
  }
}



//
//
// Buttons
//
//

.button {
  display: inline-block;
  padding: 0.75em 1.25em;
  @include fontCaps;
  text-decoration: none;
  border: 0;
  border-radius: 2px;
  background-color: rgba($color-default, 1);
  color: #ffffff;
  cursor: pointer;
  transition: background-color 0.2s $romiem-easingCurve;

  &:hover {
    background-color: rgba($color-default, 0.6);
  }
}




//
//
// Animations
//
//

.bulletList {

  > li {
    margin-bottom: 0.25em;
    padding-left: 0.5em;
  }
}



//
//
// Animations
//
//

.a-routeFadeIn {
  animation: a-routeFadeIn 0.75s $romiem-easingCurveOut 0.25s;
  animation-fill-mode: both;
}

@keyframes a-routeFadeIn {
  0%    { opacity: 0; height: 0; overflow: hidden; }
  0.01% { height: auto; overflow: visible; }
  100%  { opacity: 1; }
}

.a-routeFadeIn.fade-exit {
  animation: none;
  animation-fill-mode: none;
  opacity: 1;
  transition: opacity 0.2s $romiem-easingCurveOut;
  transition-delay: 0;
}

.a-routeFadeIn.fade-exit-active {
  opacity: 0;
}


