@import '_styles/variables';
@import '_styles/mixins';

.ShopCardItem {
  background-color: rgba(#222426, 0.04);
  padding: 32px;

  &__imageWrapper {
    position: relative;
    padding-top: (2/3) * 100%;
    line-height: 0;
  }

  &__image {
    position: absolute;
    left: 50%;
    top: 0;
    width: 100%;
    height: 100%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;


    &:hover {
      opacity: 0.8;
    }
  }

  &__lazyImage {
    box-shadow: 0px 12px 20px -8px rgba(0,0,0,.2);
    border: 1px solid #fff;
  }

  &__text {
    text-align: center;
  }

  &__title {
    margin-top: 1.25em;
  }
}
