@import '_styles/variables';
@import '_styles/mixins';

.ImageLinks {
  margin: calc(12px + 2vw) auto;
  max-width: 640px;

  @include respondAbove(m) {
    max-width: none;
  }

  &__grid {
    display: grid;
    grid-template-columns: 1fr;
    gap: calc(12px + 2vw);
    justify-content: center;
    align-items: center;

    @include respondAbove(m) {
      grid-template-columns: repeat(3, 1fr);
      gap: 2px;
    }

    @include respondAbove(xl) {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &__item {
    display: grid;
    grid-template-columns: minmax(120px, 180px) 1fr;
    gap: 20px;
    align-items: center;
    justify-content: center;
    position: relative;

    @include respondAbove(m) {
      grid-template-columns: 1fr;
    }

    &:hover {
      opacity: 0.8;
    }
  }

  &__itemImage {
    position: relative;
    width: 100%;
    background-position: center;
    background-size: cover;
  }

  &__itemImageProtection {
    display: none;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(0,0,0,0.5) 10%, rgba(0,0,0,0) 50%);

    @include respondAbove(m) {
      display: block;
    }
  }

  &__itemText {

    @include respondAbove(m) {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      padding: 1em 1.25em;
      color: #fff;
      text-shadow: 0px 0px 15px rgba(0,0,0,0.75);
    }
  }

  &__itemTitle {
    margin: 0 0 0.125em;
    line-height: 1.25;
  }

  &__itemTimeAgo {
    @include fontCaps;
    font-size: 0.625em;
    opacity: 0.3;
    margin:  0;
  }
}
