@import '_styles/variables';
@import '_styles/mixins';

.MoreReading {
  background: transparent url('../_images/misc/bannerPattern.png') repeat;
  padding: calc(12px + 2vw) 0;

  &__title {
    margin-top: 0;
    text-align: center;;
    font-size: 2rem;
  }

  .ImageLinks__item:nth-child(4) {

    @include respondAbove(m) {
      display: none;
    }

    @include respondAbove(xl) {
      display: grid;
    }
  }
}
